import Vue from 'vue';
import Vuex from 'vuex';

import showTopic from './modules/showTopic'
import topicNav from './modules/topicNav'
import userMes from './modules/userMes'
Vue.use(Vuex);

const state={
	//,commontrue
	//loginedtruefalse
	navs:[{
		name:'',
		rname:'index',
		common:true,
	},{
		name:'',
		rname:'login',
		logined:false,
	},{
		name:'',
		rname:'uread',
		logined:true,
	},{
		name:'',
		rname:'exit',
		logined:true,
	},],
	title:'cnode'
};

export default new Vuex.Store({
	modules:{
		showTopic,
		topicNav,
		userMes
	},
	state
});



