export const getTab =(tab)=>{
	let str;
	switch (tab) {
		case 'good':
			str='';
			break;
		case 'share':
			str='';
			break;
		case 'ask':
			str='';
			break;
		case 'job':
			str='';
			break;
		default:
	}
	return str
}

//
//111111

export const getTime=(val)=>{
	const t=parseFloat((new Date()-new Date(val))/1000);
	let s;
	if(t<60){
		s=parseInt(t)+''
	}else if(t>=60&&t<60*60){
		s=parseInt(t/60)+'';
	}else if(t>=60*60&&t<60*60*24){
		s=parseInt(t/60/60)+'';
	}else if(t>60*60*24&&t<60*60*24*30){
		s=parseInt(t/60/60/24)+'';
	}else if(t>60*60*24*30&&t<60*60*24*30*12){
		s=parseInt(t/60/60/24/30)+''
	}else{
		s=parseInt(t/60/60/24/30/12)+''
	}
	return s
}



