import {GET_TOP_LIST,INIT_LIST,GET_TOP_LIST_FAILUER,GET_DETAIL_TOPICS} from "../mutation_type"

const state={
	data:{
		failure:false,
		all:[],
		loading:true,
		content:'',
		comment:'', //
	},
	
	
}
const mutations={
	[GET_TOP_LIST](state,data){
		//
		if(data.length<=0){
			state.data.loading=false;
		}else{
			state.data.loading=true;
			state.data.failure=false;
			state.data.all=state.data.all.concat(data)
		}
		
	},
	//
	[INIT_LIST](state){
		state.data.loading=true;
		state.data.failure=false;
		state.data.all=[];
	},
	//
	[GET_TOP_LIST_FAILUER](state){
		state.data.failure=true;
	},
	//
	[GET_DETAIL_TOPICS](state,data){
		state.data.comment=data.replies
		state.data.content=data;
		state.data.loading=false
	}
}
export default {
	state,mutations
}



